@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;800&display=swap);
.movieShow{
    display: flex;
    flex-direction: column;
    margin-top: 1rem;    
    position: relative;  
    min-width: 30rem;    
    overflow : hidden;  
    
}
.movieShow img {
    width: 100%;
    align-self: center;
    border: .1rem solid #010101;
}

.movieShow img:hover{
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
    -webkit-transform: scale(.99);
            transform: scale(.99);
    opacity: 1;
    transition: all ease 0.4s;
    border: .1rem solid greenyellow;
}

.Yes{
    opacity: .35;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
}

.movieShow h2{
    padding:1rem;
    margin:0;
    align-self: center;
    text-align: center;
    line-height: 1;
}

.overviewInfo {
    position: absolute;
    opacity: .9;
    background-color: #111;
    bottom:0;
    left:0;
    right:0;
    max-height: 100%;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    z-index: -1;
    
}
.movieShow:hover .overviewInfo{
    cursor: pointer;
    transition: all ease 0.5s;
    z-index: 99;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}

.overviewInfo p{
    padding:2rem;
    font-size: 1rem;
    margin:0;
    align-self: center;
    text-align: justify;
}




*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #010101;
    color:whitesmoke;
    font-family: 'Outfit', sans-serif;
}

html {
    font-size: 62.5%;
}


nav {
    display: flex;
    justify-content: space-between;
    max-height: 10rem;
}

nav > a {
    max-width: 10%;
}

#logo {
    height:100%;
    padding: 0 2rem;
}


img:hover {
    cursor:pointer;
}


nav > ul {
    display: flex;
    width: 80%;
    grid-gap:10rem;
    gap:10rem;
    list-style: none;
    margin: 0;
    padding: 0 5rem;
    justify-content: flex-end;
}

nav ul li {
    align-self: center;
    font-size:2rem;
}

nav ul li:hover{
    color: greenyellow;
    cursor:pointer;
}

nav button {
    width: 100px;
    font-weight: 800;
    background-color:greenyellow;
    border:.1rem solid greenyellow;
}
nav button:hover {
    cursor:pointer;
    color: #111;
    background-color:greenyellow
}

section{
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    overflow: hidden;    
}

footer {
    text-align: center;
    justify-content: center;
    position: relative;    
    padding: 2rem;
}




